<template>
  <div>
    <Navbar />
    <v-container>
      <v-row>
        <v-col v-for="history in historyList" :key="history._id">
          <v-card class="mx-auto" color="primary" dark max-width="400">
            <v-card-text>
              Routes: {{ history.routes }} <br />
              Price: {{ history.totalPrice }} <br />
              travel time: {{ history.travelTime }} days
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar";
import { mapActions, mapGetters } from "vuex";

export default {
  name: "History",
  components: { Navbar },
  methods: { ...mapActions(["getHistory"]) },
  created() {
    this.getHistory();
  },
  computed: {
    ...mapGetters(["historyList"]),
  },
};
</script>
